import React, {Component} from 'react';
import {Page, PageContent, Link} from '~/src/Core';
import Dialog from '~/src/Dialog';
import {showPreloader, hidePreloader} from '~/src/Preloader';

export default class DialogDemo extends Component {

  state = {
    visible: false,
    text: 'Alert content'
  }

  open = ()=>{
    this.setState({
      visible: true
    })
  }

  close = ()=>{
    this.setState({
      visible: false
    })
  }

  alert = ()=>{
    Dialog.alert({text: 'Alert content'});
    Dialog.alert({text: 'Alert content2'});
    Dialog.confirm({
      text: '',
      okText: '',
      cancelText: '',
      onOk: ()=> {
        Dialog.prompt({
          title: '',
          text: '?',
          onOk: (val)=>Dialog.alert({text: val}),
          onCancel: ()=>Dialog.alert({text: 'Cancel'}),
        })
      },
      onCancel: ()=>Dialog.alert({text: 'Confirm -> cancel'}),
    });
  }

  toast = ()=>{
    Dialog.toast('toast');
    // Dialog.toast.success('2toast');
    // Dialog.toast.fail('3toast');
    // Dialog.toast.warning('4toast');
    // Dialog.toast.offline('5toast');
  }

  render(){
    return (
      <Page>
        <PageContent>
          <a onClick={this.alert}>alert</a>,

          <a onClick={this.toast}>toast</a>
        </PageContent>
      </Page>
    )
  }
}
